.CurrentFood__food-image{
    width: 350px;
    height: 350px;
    background: repeating-linear-gradient(
            45deg
            , white, #EFEFEF, #FFBE47, #EFEFEF);
    border-radius: 50px;
    background-size: 300% 300%;
    -webkit-animation: background-shake 1s ease infinite;
            animation: background-shake 1s ease infinite;

}

@-webkit-keyframes background-shake {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 25%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes background-shake {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 25%;
    }
    100% {
        background-position: 0% 50%;
    }
}
